	module.exports = {"data":{"wordpressPage":{"title":"Comparing methods","content":"<h2>Key information:</h2>\n<ul>\n<li>Dilatation and evacuation (D&amp;E) and medical abortion with mifepristone and misoprostol or misoprostol only are safe and effective methods of abortion.</li>\n<li>Women should be offered a choice of methods when both D&amp;E and medical abortion are available.</li>\n<li>Medical abortion has a higher rate of retained products of conception, failed abortion and minor adverse events.</li>\n<li>D&amp;E requires a trained, experienced provider and specialized equipment.</li>\n</ul>\n<h3>Quality of evidence:</h3>\n<p><strong>Moderate</strong></p>\n<h3>Comparison of methods</h3>\n<p>In retrospective cohort studies, women with gestations 13-24 weeks who had medical abortions had an increased rate of failed abortion and retained products of conception with a need for further intervention compared to women who had D&amp;E (Autry, Hayes, Jacobson, &amp; Kirby, 2002; Bryant, Grimes, Garrett, &amp; Stuart, 2011; Sonalkar, Ogden, Tran, &amp; Chen, 2017). However, the rate of major adverse events including infection, transfusion, hysterectomy and death does not differ between the two methods.</p>\n<p>The largest trial comparing methods randomized 122 women with gestations between 13-20 weeks to D&amp;E or medical abortion with mifepristone and misoprostol (Kelly, Suddes, Howel, Hewison, &amp; Robson, 2010). Overall rates of complications were similar in the two groups, although the types of complications differed. Five women in the medical arm required uterine evacuation for retained products of conception and one suffered bleeding requiring transfusion; only one woman in the surgical arm required repeat uterine evacuation, one suffered a cervical laceration, and five had hemorrhage that did not require transfusion. A statistically significant proportion of women randomized to medical abortion had more bleeding and pain and found abortion less acceptable than women who had D&amp;E. A pilot randomized trial of 18 women with gestations between 14-19 weeks comparing D&amp;E and medical abortion with mifepristone and misoprostol found a higher rate of adverse events, specifically retained placenta and fever, in women undergoing medical abortion, although none were serious (Grimes, Smith, &amp; Witham, 2004).</p>\n<p>In published studies of medical abortion compared to D&amp;E, rates of intervention for medical abortion may be artificially high because failure was defined as no delivery within 24 hours (Bryant et al., 2011) and retained placenta was diagnosed after two hours (Grimes et al., 2004). In practice, more time may be allowed for successful medical abortion to occur.</p>\n<h3>The importance of choice</h3>\n<p>In settings where both D&amp;E and medical abortion are available, if a woman is a candidate for either procedure, she should be offered a choice. Both randomized trials referenced above (Kelly et al., 2010; Grimes, et al., 2004) had difficulty with recruitment due to women’s strong preferences for one type of abortion over the other. Choice of methods is very individual—some women prefer the speed and predictability of D&amp;E, while others prefer a more “labor-like” process with an intact fetus (Kelly et al., 2010; Kerns et al., 2012). Some women may want to see or hold an intact fetus, and an intact fetus may allow for a more comprehensive fetal autopsy where it is needed.</p>\n<h3>References</h3>\n<p>Autry, A. M., Hayes, E. C., Jacobson, G. F., &amp; Kirby, R. S. (2002). A comparison of medical induction and dilation and evacuation for second-trimester abortion. <em>American Journal of Obstetrics &amp; Gynecology</em>, <em>187</em>(2), 393-397.</p>\n<p>Bryant, A. G., Grimes, D. A., Garrett, J. M., &amp; Stuart, G. S. (2011). Second-trimester abortion for fetal anomalies or fetal death: Labor induction compared with dilation and evacuation. <em>Obstetrics &amp; Gynecology</em>, <em>117</em>(4), 788-792.</p>\n<p>Grimes, D. A., Smith, M. S., &amp; Witham, A. D. (2004). Mifepristone and misoprostol versus dilation and evacuation for midtrimester abortion: A pilot randomised controlled trial. <em>BJOG: An International Journal of Obstetrics &amp; Gynaecology</em>, <em>111</em>(2), 148-153.</p>\n<p>Kelly, T., Suddes, J., Howel, D., Hewison, J., &amp; Robson, S. (2010). Comparing medical versus surgical termination of pregnancy at 13-20 weeks of gestation: A randomised controlled trial. <em>BJOG: An International Journal of Obstetrics &amp; Gynaecology</em>, <em>117</em>(12), 1512-1520.</p>\n<p>Kerns, J., Vanjani, R., Freedman, L., Meckstroth, K., Drey, E. A., &amp; Steinauer, J. (2012). Women&#8217;s decision making regarding choice of second trimester termination method for pregnancy complications. <em>International Journal of Gynecology &amp; Obstetrics</em>, <em>116</em>(3), 244-248.</p>\n<p>Sonalkar, S., Ogden, S. N., Tran, L. K., &amp; Chen, A. Y. (2017). Comparison of complications associated with induction by misoprostol versus dilation and evacuation for second-trimester abortion. <em>International Journal of Gynecology &amp; Obstetrics, 138, </em>272-275.</p>\n","fields":{"path":"at-or-after-13/comparing-methods"},"acf":{"last_reviewed":"20171211","landing_page":null}},"parent":{"title":"Recommendations for abortion at or after 13 weeks gestation","fields":{"path":"at-or-after-13"},"wordpress_parent":159},"grandparent":null,"site":{"id":"Site","siteMetadata":{"title":"Ipas Clinical Updates"}}},"pathContext":{"id":"896f1000-80b1-52c5-b662-f9598b2f61f1","title":"Comparing methods","content":"<h2>Key information:</h2>\n<ul>\n<li>Dilatation and evacuation (D&amp;E) and medical abortion with mifepristone and misoprostol or misoprostol only are safe and effective methods of abortion.</li>\n<li>Women should be offered a choice of methods when both D&amp;E and medical abortion are available.</li>\n<li>Medical abortion has a higher rate of retained products of conception, failed abortion and minor adverse events.</li>\n<li>D&amp;E requires a trained, experienced provider and specialized equipment.</li>\n</ul>\n<h3>Quality of evidence:</h3>\n<p><strong>Moderate</strong></p>\n<h3>Comparison of methods</h3>\n<p>In retrospective cohort studies, women with gestations 13-24 weeks who had medical abortions had an increased rate of failed abortion and retained products of conception with a need for further intervention compared to women who had D&amp;E (Autry, Hayes, Jacobson, &amp; Kirby, 2002; Bryant, Grimes, Garrett, &amp; Stuart, 2011; Sonalkar, Ogden, Tran, &amp; Chen, 2017). However, the rate of major adverse events including infection, transfusion, hysterectomy and death does not differ between the two methods.</p>\n<p>The largest trial comparing methods randomized 122 women with gestations between 13-20 weeks to D&amp;E or medical abortion with mifepristone and misoprostol (Kelly, Suddes, Howel, Hewison, &amp; Robson, 2010). Overall rates of complications were similar in the two groups, although the types of complications differed. Five women in the medical arm required uterine evacuation for retained products of conception and one suffered bleeding requiring transfusion; only one woman in the surgical arm required repeat uterine evacuation, one suffered a cervical laceration, and five had hemorrhage that did not require transfusion. A statistically significant proportion of women randomized to medical abortion had more bleeding and pain and found abortion less acceptable than women who had D&amp;E. A pilot randomized trial of 18 women with gestations between 14-19 weeks comparing D&amp;E and medical abortion with mifepristone and misoprostol found a higher rate of adverse events, specifically retained placenta and fever, in women undergoing medical abortion, although none were serious (Grimes, Smith, &amp; Witham, 2004).</p>\n<p>In published studies of medical abortion compared to D&amp;E, rates of intervention for medical abortion may be artificially high because failure was defined as no delivery within 24 hours (Bryant et al., 2011) and retained placenta was diagnosed after two hours (Grimes et al., 2004). In practice, more time may be allowed for successful medical abortion to occur.</p>\n<h3>The importance of choice</h3>\n<p>In settings where both D&amp;E and medical abortion are available, if a woman is a candidate for either procedure, she should be offered a choice. Both randomized trials referenced above (Kelly et al., 2010; Grimes, et al., 2004) had difficulty with recruitment due to women’s strong preferences for one type of abortion over the other. Choice of methods is very individual—some women prefer the speed and predictability of D&amp;E, while others prefer a more “labor-like” process with an intact fetus (Kelly et al., 2010; Kerns et al., 2012). Some women may want to see or hold an intact fetus, and an intact fetus may allow for a more comprehensive fetal autopsy where it is needed.</p>\n<h3>References</h3>\n<p>Autry, A. M., Hayes, E. C., Jacobson, G. F., &amp; Kirby, R. S. (2002). A comparison of medical induction and dilation and evacuation for second-trimester abortion. <em>American Journal of Obstetrics &amp; Gynecology</em>, <em>187</em>(2), 393-397.</p>\n<p>Bryant, A. G., Grimes, D. A., Garrett, J. M., &amp; Stuart, G. S. (2011). Second-trimester abortion for fetal anomalies or fetal death: Labor induction compared with dilation and evacuation. <em>Obstetrics &amp; Gynecology</em>, <em>117</em>(4), 788-792.</p>\n<p>Grimes, D. A., Smith, M. S., &amp; Witham, A. D. (2004). Mifepristone and misoprostol versus dilation and evacuation for midtrimester abortion: A pilot randomised controlled trial. <em>BJOG: An International Journal of Obstetrics &amp; Gynaecology</em>, <em>111</em>(2), 148-153.</p>\n<p>Kelly, T., Suddes, J., Howel, D., Hewison, J., &amp; Robson, S. (2010). Comparing medical versus surgical termination of pregnancy at 13-20 weeks of gestation: A randomised controlled trial. <em>BJOG: An International Journal of Obstetrics &amp; Gynaecology</em>, <em>117</em>(12), 1512-1520.</p>\n<p>Kerns, J., Vanjani, R., Freedman, L., Meckstroth, K., Drey, E. A., &amp; Steinauer, J. (2012). Women&#8217;s decision making regarding choice of second trimester termination method for pregnancy complications. <em>International Journal of Gynecology &amp; Obstetrics</em>, <em>116</em>(3), 244-248.</p>\n<p>Sonalkar, S., Ogden, S. N., Tran, L. K., &amp; Chen, A. Y. (2017). Comparison of complications associated with induction by misoprostol versus dilation and evacuation for second-trimester abortion. <em>International Journal of Gynecology &amp; Obstetrics, 138, </em>272-275.</p>\n","last_reviewed":"20171211","parent":246,"grandparent":null}}

